.coaches__title {
  margin: 0 0 48px;

  @include vp-1199 {
    margin: 0 0 31px;
  }

  @include vp-767 {
    margin: 0 0 50px;
  }
}

.coaches__swiper {
  position: relative;
}

.coaches__nojs {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 40px;

  @include vp-1199 {
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.coaches__button {
  position: absolute;
  top: 64%;
  z-index: 4;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;

  @include vp-767 {
    width: 10px;
    height: 19px;
  }

  &--prev {
    left: 47px;

    @include vp-767 {
      left: 22px;
    }
  }

  &--next {
    right: 47px;
    transform: rotate(180deg);

    @include vp-767 {
      right: 22px;
    }
  }

  &--nojs {
    display: none;
  }
}

.coaches__icon {
  @include vp-767 {
    width: 10px;
    height: 19px;
  }
}
